



























.note{
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 8px 0;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note pre {
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: inherit;
}
.note button{
  background: none;
  border: none;
  font-size: 20px;
  opacity: 0;
  cursor: pointer;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  margin: 0 4px 0 0;
}
.note button.edit{
  float: right;
}
.note:hover, .note:focus{
  box-shadow: 0 2px 10px #999;
}
.note:hover button, .note:focus button{
  opacity: 0.6;
}
.note button:hover, .note button:focus{
  opacity: 1;
}



